import tools from '@/utils/tools'

/**
 * 
 * @param {file} file el-upload
 * @param {number} size (kb) 10M
 */
export const validImgUpload = (file, size) => {
  size = +size || 10240
  const isSizeOut = file.size / 1024 > size
  if (isSizeOut) {
    Message.error('' + tools.toStorage(size * 1024))
  }
  return !isSizeOut
}