/**
 *  
 */
<template>
  <div class="uploadWrapper">
    <vuedraggable
      :option="{ disabled: true }"
      class="vue-draggable"
      :class="{ single: isSingle, maxHidden: isMaxHidden }"
      v-model="imgList"
      tag="ul"
      draggable=".draggable-item"
      @start="onDragStart"
      @end="onDragEnd"
    >
      <!--  -->
      <li
        v-for="(item, index) in imgList"
        :key="item + index"
        class="draggable-item"
        :style="{ width: width + 'px', height: height + 'px' }"
      >
        <el-image :src="item" :preview-src-list="[item]"></el-image>
        <div class="shadow" @click="onRemoveHandler(index)">
          <i class="el-icon-delete"></i>
        </div>
      </li>
      <!--  -->
      <el-upload
        slot="footer"
        ref="uploadRef"
        class="uploadBox"
        :style="{ width: width + 'px', height: height + 'px' }"
        action="https://httpbin.org/post"
        accept=".jpg,.jpeg,.png,.gif"
        :show-file-list="false"
        :multiple="!isSingle"
        :limit="limit"
        :before-upload="beforeUpload"
        :on-success="onSuccessUpload"
        :on-exceed="onExceed"
      >
        <i class="el-icon-plus uploadIcon">
          <span class="uploading" v-show="isUploading">...</span>
          <span
            v-if="!isUploading && limit && limit !== 99 && !isSingle"
            class="limitTxt"
            >{{ limit }}</span
          >
        </i>
      </el-upload>
    </vuedraggable>
  </div>
</template>

<script>
import vuedraggable from "vuedraggable";
import { validImgUpload } from "../../utils/validate";
import lrz from "lrz"; // 
import tools from "../../utils/tools";

export default {
  name: "ImgUpload",

  props: {
    // (url) v-model
    value: {
      type: Array,
      default() {
        return [];
      },
    },
    // 
    limit: {
      type: Number,
      default: 99,
    },
    // (kb)
    size: {
      type: Number,
      default: 500,
    },
    // ()
    isSingle: {
      type: Boolean,
      default: false,
    },
    // 
    useCompress: {
      type: Boolean,
      default: false,
    },
    // (px)
    width: {
      type: Number,
      default: 100,
    },
    // (px)
    height: {
      type: Number,
      default: 100,
    },
  },

  data() {
    return {
      isUploading: false, // 
      isFirstMount: true, // 
    };
  },

  computed: {
    // 
    imgList: {
      get() {
        return this.value;
      },
      set(val) {
        if (val.length < this.imgList.length) {
          // el-upload
          this.syncElUpload(val);
        }
        // v-model
        this.$emit("input", val);
      },
    },
    // 
    isMaxHidden() {
      return this.imgList.length >= this.limit;
    },
  },

  watch: {
    value: {
      handler(val) {
        if (this.isFirstMount && this.value.length > 0) {
          this.syncElUpload();
        }
      },
      deep: true,
    },
  },

  mounted() {
    if (this.value.length > 0) {
      this.syncElUpload();
    }
  },

  methods: {
    // el-upload
    syncElUpload(val) {
      const imgList = val || this.imgList;
      this.$refs.uploadRef.uploadFiles = imgList.map((v, i) => {
        return {
          name: "pic" + i,
          url: v,
          status: "success",
          uid: tools.createUniqueString(),
        };
      });
      this.isFirstMount = false;
    },
    // 
    beforeUpload(file) {
      this.isFirstMount = false;
      if (this.useCompress) {
        // 
        return new Promise((resolve, reject) => {
          lrz(file, { width: 1920 })
            .then((rst) => {
              file = rst.file;
            })
            .always(() => {
              if (validImgUpload(file, this.size)) {
                this.isUploading = true;
                resolve();
              } else {
                reject(new Error());
              }
            });
        });
      } else {
        if (validImgUpload(file, this.size)) {
          this.isUploading = true;
          return true;
        } else {
          return false;
        }
      }
    },
    // 
    onSuccessUpload(res, file, fileList) {
      // 
      if (res.files) {
        // 
        if (this.imgList.length < this.limit) {
          // urlimgList
          this.imgList.push(res.files.file);
        }
      } else {
        // 
        this.syncElUpload();
        this.$message({ type: "error", message: res.msg });
      }
      this.isUploading = false;
    },
    // 
    onRemoveHandler(index) {
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      })
        .then(() => {
          this.imgList = this.imgList.filter((v, i) => {
            return i !== index;
          });
        })
        .catch(() => {});
    },
    // 
    onExceed() {
      this.$refs.uploadRef.abort(); // 
      this.syncElUpload();
      this.$message({
        type: "warning",
        message: `${this.limit}`,
      });
    },
    onDragStart(e) {
      e.target.classList.add("hideShadow");
    },
    onDragEnd(e) {
      e.target.classList.remove("hideShadow");
    },
  },

  components: { vuedraggable },
};
</script>

<style lang="less" scoped>
/deep/ .el-upload {
  width: 100%;
  height: 100%;
}

// 
.uploadIcon {
  width: 100%;
  height: 100%;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px dashed #c0ccda;
  background-color: #fbfdff;
  border-radius: 6px;
  font-size: 20px;
  color: #999;

  .limitTxt,
  .uploading {
    position: absolute;
    bottom: 10%;
    left: 0;
    width: 100%;
    font-size: 14px;
    text-align: center;
  }
}

// 
.vue-draggable {
  display: flex;
  flex-wrap: wrap;

  .draggable-item {
    margin-right: 5px;
    margin-bottom: 5px;
    border: 1px solid #ddd;
    border-radius: 6px;
    position: relative;
    overflow: hidden;

    .el-image {
      width: 100%;
      height: 100%;
    }
    .shadow {
      position: absolute;
      top: 0;
      right: 0;
      background-color: rgba(0, 0, 0, 0.5);
      opacity: 0;
      transition: opacity 0.3s;
      color: #fff;
      font-size: 20px;
      line-height: 20px;
      padding: 2px;
      cursor: pointer;
    }
    &:hover {
      .shadow {
        opacity: 1;
      }
    }
  }
  &.hideShadow {
    .shadow {
      display: none;
    }
  }
  &.single {
    overflow: hidden;
    position: relative;

    .draggable-item {
      position: absolute;
      left: 0;
      top: 0;
      z-index: 1;
    }
  }
  &.maxHidden {
    .uploadBox {
      display: none;
    }
  }
}
// el-image
.el-image-viewer__wrapper {
  .el-image-viewer__mask {
    opacity: 0.8;
  }
  .el-icon-circle-close {
    color: #fff;
  }
}
</style>